.fontRoboto {
  font-family: "Roboto", sans-serif; }

.fontQuicksand {
  font-family: "Quicksand", sans-serif; }

.fontExtraSmall {
  font-size: 0.7rem; }

.fontSmall {
  font-size: 0.8rem; }

.fontNormal {
  font-size: 1rem; }

.fontMedium {
  font-size: 1.2rem; }

.fontLarge {
  font-size: 1.5rem; }

.fontExtraLarge {
  font-size: 2rem; }

.cursorPointer {
  cursor: pointer; }

.cursorNotAllowed {
  cursor: not-allowed; }

.cursorCrosshair {
  cursor: crosshair; }

/**
