| You can style your app here
|--------------------------------------------------
*/
html,
body {
  width: 100% !important;
  height: 100% !important;
  font-size: 16px;
  line-height: initial; }

body {
  margin: 0px;
  padding: 0px;
  font-family: "Quicksand", sans-serif;
  background-color: #ffffff; }

h1, h2, h3, h4, h5, h6 {
  font-weight: 900;
  font-family: "Roboto", sans-serif; }



